body {
  margin: 0;
}

.page-container {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
  min-height: calc(100vh - 56px);
  padding: 10px 2rem 2rem 2rem;
}

.MuiBreadcrumbs-li {
  font-size: 12px;
}

.checkbox-button {
  border: 1px solid #EEEEEE;
  background-color: #EEEEEE;
  border-radius: 10px;
  padding: 0 20px;
  min-width: 140px;
  width: 80%;
  display: flex;
  align-items: center;
  position: relative;
}

.checkbox-button .icon {
  position: absolute;
  right: 0;
  opacity: 0.6;
}

.custom-dialog {
  margin-top: -20vh;
}

.game-box {
  font-family: "Roboto","Helvetica","Arial",sans-serif;
}
.game-box fieldset {
  border: 2px solid rgb(189 227 191);
}
.game-box fieldset legend {
  background-color: rgb(75 179 80);
  color: white;
  padding: 1px 10px;
  border-radius: 6px;
  font-size: 14px;
}

.card-content {
  padding: 8px !important;
}
.player-name {
  font-size: 14px;
  padding: 3px 1px;
}
.on-break {
  font-family: "Roboto","Helvetica","Arial",sans-serif;
  font-size: 14px;
  padding: 4px 14px;
  background-color: #EEEEEE;
  margin: 0 2px;
}

.on-break span:first-child {
  color: darkorange;
  padding-right: 8px;
}

